.stars {
  display: flex;
}

.stars__star {
  background: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child,
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(n) {
  background: url(../images/star-active.svg);
  background-repeat: no-repeat;
  background-position: 50%;
}
